import React from 'react';

export default class WordImg extends React.Component{
    constructor(){
        super()
        this.state={
            newsData:{},
            content:[
                {item:'内容一'},
                {item:'内容二'},
                {item:'内容三'}
            ],
        }

    }
    render(){

        var newdateArr = "badiu"

        var _this = this;
        $.ajax({
            url:"/api/user/signup",

            type:"get",
            async:'false'
        }).done(function(data){

           console.log( $.type(data))
            newdateArr = data[0].name
            _this.setState({newsData:JSON.parse(data)})
        })

        return(
            <div className="newsImg">
                {this.state.newsData.map(function (item) {
                    return <li className="fx1">{item.name}</li>
                })}
            </div>
        )
    }
}


